<template>
  <div class="blog-editor">
    <el-card class="blog-editor--head">
      <h2 class="blog-card--title">写文章</h2>
      <el-input
        ref="title"
        class="editor-head--input"
        placeholder="请输入文章标题(必填)"
        v-model="title"
        clearable
      ></el-input>
    </el-card>

    <el-card class="blog-editor--wrap">
      <div style="border: 1px solid #ebeef5">
        <Toolbar
          style="border-bottom: 1px solid #ebeef5"
          :editor="editor"
          :mode="mode"
        />
        <Editor
          class="wang-editor"
          style="height: 500px; overflow-y: hidden overflow-x: auto"
          v-model="html"
          :defaultConfig="editorConfig"
          :mode="mode"
          @onCreated="onCreated"
        />
      </div>
    </el-card>

    <el-card class="blog-editor--column">
      <h3>分区选择</h3>
      <el-radio-group
        v-if="columnList.length"
        class="editor-column--group"
        v-model="columnSelected"
      >
        <el-radio-button
          v-for="column in columnList"
          :label="column.id"
          :key="column.id"
          >{{ column.name }}</el-radio-button
        >
      </el-radio-group>
    </el-card>

    <el-card class="blog-editor--control">
      <el-button type="primary" @click="submitArticle">发布文章</el-button>
      <el-button type="primary" @click="clearArticle">清空内容</el-button>
    </el-card>
  </div>
</template>

<script>
import { Editor, Toolbar } from '@wangeditor/editor-for-vue'
import '@wangeditor/editor/dist/css/style.css'
import store from 'store'
import HTTP_CONFIG from '@/configs/http.config'

const editorConfig = {
  placeholder: '请输入内容...',
  MENU_CONF: {
    //上传图片参数配置
    uploadImage: {
      server: process.env.VUE_APP_BASE_UPLOAD_URL + '/article',
      // 单个文件的最大体积限制，默认为 2M
      maxFileSize: 5 * 1024 * 1024,
      // 最多可上传几个文件，默认为 10
      maxNumberOfFiles: 10,
      // 选择文件时的类型限制，默认为 ['image/*'] 。如不想限制，则设置为 []
      allowedFileTypes: ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp'],
      // 自定义增加 http  header
      headers: {
        Authorization: `Bearer ${store.get(HTTP_CONFIG.TOKEN_NAME)}`
      },
      //上传文件之前
      // onBeforeUpload(file) {
      //   // 可以 return
      //   // 1. return file 或者 new 一个 file ，接下来将上传
      //   // 2. return false ，不上传这个 file
      // },
      // // 单个文件上传失败
      // onFailed(file, res) {
      //   console.log(`${file.name} 上传失败`, res)
      // },

      // 上传错误，或者触发 timeout 超时
      //参数(file, err, res)
      onError() {
        Vue.prototype.$notify.error({
          title: '上传失败',
          message: '图片大小超出了最大上传限制(5M)'
        })
      }
    }
  }
}

export default {
  name: 'VueBlogEditor',
  components: { Editor, Toolbar },
  props: {
    columnId: {
      type: String
    }
  },
  data() {
    return {
      editor: null,
      html: '',
      title: '',
      cover: '',
      editorConfig: editorConfig,
      mode: 'default', // or 'simple'
      columnList: [],
      columnSelected: ''
    }
  },
  created() {
    //this.initCDN()
    //this.$nextTick(() => {
    //获取分区列表 如果请求带上了columnID 则切换到对应的分区
    this.getColumns()
      .then(() => {
        this.columnId && (this.columnSelected = this.columnId)
      })
      .catch((err) => err)//console.log(err))
    //})
  },
  methods: {
    // initCDN() {
    //   const js = document.createElement('script')
    //   const css = document.createElement('link')
    //   js.src =
    //     'https://cdn.bootcdn.net/ajax/libs/wangeditor5/5.1.15/index.min.js'
    //   css.rel = 'stylesheet'
    //   css.href =
    //     'https://cdn.bootcdn.net/ajax/libs/wangeditor5/5.1.15/css/style.min.css'
    //   document.body.append(css, js)
    // },
    onCreated(editor) {
      this.editor = Object.seal(editor) // 一定要用 Object.seal() ，否则会报错
    },
    async getColumns() {
      try {
        let { list } = await this.$http({ type: 'getColumns' })
        this.columnList = list.map(({ id, name }) => ({ id, name }))
        this.columnSelected = this.columnList?.[0].id
      } catch (error) {
        //
      }
    },

    //文章提交-验证
    async submitArticle() {
      if (!this.validateArticle()) return
      let content = this.editor.getHtml()
      try {
        await this.$http({
          type: 'postArticle',
          data: {
            title: this.title,
            //封面图 默认为第一张图片如果没有就为空
            cover: content.match(/<img src="([^\s]+)"/)?.[1] || undefined,
            column: this.columnSelected,
            content
          }
        })
        this.$router.push('/index')
        this.$notify.success({
          title: '文章发布成功'
        })
      } catch (err) {
        this.$notify.error({
          title: '文章发布失败'
        })
      }
    },

    validateArticle() {
      if (this.title.trim().length === 0) {
        this.$notify.warning({
          title: '警告',
          message: '文章标题必填'
        })
        this.$refs.title.focus()
        return false
      }
      let content = this.editor.getText()
      if (content.trim().length === 0) {
        this.$notify.warning({
          title: '警告',
          message: '内容不能为空'
        })
        return false
      }
      return true
    },
    clearArticle() {
      this.html = ''
    }
  },
  beforeDestroy() {
    const editor = this.editor
    if (editor == null) return
    editor.destroy() // 组件销毁时，及时销毁编辑器
  }
}
</script>

<style lang="stylus">
.blog-editor>.el-card
  margin-bottom 10px
  h3
    font-size 24px
    font-weight 900

// head
.editor-head--input
  padding 10px
  input, .el-input__icon.el-input__clear
    font-size 18px
  .el-input__suffix
    right 16px
    top 2px

// column
.editor-column--group
  display flex
  flex-wrap wrap
  padding 10px
  .el-radio-button, .el-radio-button:first-child, .el-radio-button:last-child
    margin 0 10px 10px 0
    .el-radio-button__inner
      border-radius 4px
      border 1px solid #ccc

// control
.blog-editor--control .el-card__body
  display flex
  .el-button
    flex 1
    font-size 18px
</style>